<template>
    <div class="container-fluid px-0">
        <h2>Trending</h2>
        <div class="row gx-0 row-cols-2">
            <CatalogPlaceholder v-if="loading" />
            <div v-else class="col-sm-2" v-for="item in trending" :key="item">
                <div class="card text-center text-bg-dark">
                    <router-link :to="'/watch-' + item.media_type + '/' + item.id">
                        <img :src="'https://image.tmdb.org/t/p/w342' + item.poster_path"
                        class="card-img-top poster" alt="Poster Image">
                    </router-link>
                    <div class="card-body">
                        <h5 class="card-title">{{item.title}}{{item.name}}</h5>
                    </div>
                    <div class="card-footer">
                      <router-link class="btn btn-primary" :to="'/watch-' + item.media_type + '/' + item.id"><font-awesome-icon icon="fa-solid fa-play" /> Watch Now</router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { getTrending } from '@/services/IMDBService'
import CatalogPlaceholder from '@/components/CatalogPlaceholder.vue'

export default defineComponent({
  name: 'TrendingCatalog',
  components: {
    CatalogPlaceholder
  },
  data () {
    return {
      trending: [],
      loading: true
    }
  },
  created () {
    this.getTrending()
  },
  methods: {
    getTrending () {
      getTrending().then(response => {
        this.trending = response.results
        this.loading = false
      })
    }
  }
})
</script>

